:export{primary:#09e;red:#f96c0e;mainBackground:#efefff;componentBackground:#fff;lightText:#999;whiteText:#fff;authShadow:0 1px 3px rgba(0,0,0,.6);pageShadow:0 2px 2px rgba(0,0,0,.6);TOPBAR_HEIGHT:60px;componentWidth:90%;lightGrey:#e3e3e3}.TopicPreview{padding:15px;margin:0 80px;transition:.15s}.TopicPreview:hover{cursor:pointer;box-shadow:-3px 0 0 0 #e3e3e3}.TopicPreview .body{width:100%;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.TopicPreview .body .topic .title{font-weight:500}.TopicPreview .body .topic .description{padding:3px}.TopicPreview .meta{padding:2px;display:flex}.TopicPreview .meta .likes{color:#f96c0e;padding-right:30px}.TopicPreview .meta .replies{color:#09e}